<template>
    <div v-if="val">
        <Modal v-model="showInfo" title="订单付款" :mask-closable="false" @on-cancel="cancel">
            <Row type="flex" class="item">
                <Row type="flex" justify="end" class="item-label"><b>订单号:</b></Row>
                <div>{{number}}</div>
            </Row>
            <Row type="flex" class="item">
                <Row type="flex" justify="end" class="item-label"><b>联系人:</b></Row>
                <div>{{shippingName}}</div>
            </Row>
            <Row type="flex" class="item">
                <Row type="flex" justify="end" class="item-label"><b>手机号:</b></Row>
                <div>{{shippingPhone}}</div>
            </Row>
            <Row type="flex" class="item">
                <Row type="flex" justify="end" class="item-label"><b>商品信息:</b></Row>
                <div>{{productInfo}}</div>
            </Row>
            <Row type="flex" class="item">
                <Row type="flex" justify="end" class="item-label"><b>数量:</b></Row>
                <div>{{quantity}}</div>
            </Row>
            <Row type="flex" class="item" align="middle">
                <Row type="flex" justify="end" class="item-label"><b>合计:</b></Row>
                <div class="price">{{ForceMoney(amount)}}</div>
            </Row>
            <Divider>支付方式</Divider>
            <RadioGroup v-model="paymentType" style="display: flex;justify-content: space-between;">
                <Radio :label="0" :disabled="balance < amount" class="payment-type">
                    <Icons type="yuezhifu" :size="30" color="#ffc849"/>
                    <v-countup :end-val="balance" :decimals="2" :count-style="{color: '#ffc849', fontSize: '18px'}">
                        <span slot="leftText" style="font-size: 14px;font-weight: normal;">可用</span>
                        <span slot="rightText" style="font-size: 14px;font-weight: normal;">余额</span>
                    </v-countup>
                </Radio>
                <Radio :label="1" :disabled="!isEnable(1)" class="payment-type">
                    <Icons type="weipay" :size="35" :color="isEnable(1) ? '#12cc0d' : 'gray'"/>
                </Radio>
                <Radio :label="2" :disabled="!isEnable(2)" class="payment-type">
                    <Icons type="ali_pay" :size="30" :color="isEnable(2) ? '#24abed' : 'gray'"/>
                </Radio>
            </RadioGroup>
            <div slot="footer">
                <Button type="text" size="large" @click="val = false">取消</Button>
                <Button type="primary" size="large" :loading="buttonLoading" @click.native="pay" :disabled="!canPay">去付款</Button>
            </div>
        </Modal>
        <Modal v-model="showWx" :width="800" title="微信扫码支付" footer-hide :mask-closable="false" @on-cancel="cancelWx">
            <div style="display: flex;justify-content: center;align-items: center;">
                <div style="display: flex;align-items: center;flex-direction: column;">
                    <div>
                        <span style="font-size: 28px;margin-right: 10px">微信支付</span>
                        <b class="price">￥{{ForceMoney(amount)}}</b>
                    </div>
                    <canvas id="qr"></canvas>
                    <div style="display: flex; align-items: center; width: 176px">
                        <Icons type="scan" :size="50"/>
                        <span style="font-size: 16px;margin-left: 10px">
                            打开手机微信
                            扫一扫二维码
                        </span>
                    </div>
                </div>
                <div class="wx-right">
                    <img :src="wxScan"/>
                </div>
            </div>
        </Modal>
    </div>
</template>

<script>
import VCountup from 'v-countup';
import { mapActions } from 'vuex';
import QrCodeWithLogo from 'qr-code-with-logo';
import { ForceMoney } from '@/libs/common';
import Icons from '@/components/icons/icons';
import wxScan from '@/assets/images/wx-scan.png';

export default {
    name: 'Pay',
    components: { Icons, VCountup },
    props: {
        order: {
            type: Object,
            require: true
        },
        shipping: {
            type: Object
        },
        items: {
            type: Array
        },
        value: {
            type: Boolean,
            default: false
        }
    },
    data () {
        return {
            wxScan,
            ForceMoney,
            val: this.value,
            account: null,
            paymentTypes: [],
            paymentType: 0,
            buttonLoading: false,
            showInfo: true,
            showWx: false
        };
    },
    computed: {
        number () {
            return this.order?.number;
        },
        amount () {
            return this.order?.payAmount || 0;
        },
        shippingName () {
            return this.shipping?.nickname;
        },
        shippingPhone () {
            return this.shipping?.phone;
        },
        productInfo () {
            return `${this.items?.[0]?.productName || ''} - ${this.items?.[0]?.productSubName || ''}`;
        },
        quantity () {
            return this.items?.reduce((p, c) => p + c.quantity, 0) || 0;
        },
        balance () {
            const v = this.account?.cash + this.account?.credit;
            if (isNaN(v)) {
                return 0;
            }
            return Number(ForceMoney(v));
        },
        canPay () {
            return this.paymentType !== null;
        }
    },
    watch: {
        async value (val) {
            this.val = val;
            if (val) {
                this.account = await this.getAccountInfo();
                await this.getPaymentTypes();
                this.showInfo = true;
            }
        },
        val (val) {
            this.$emit('input', val);
        }
    },
    methods: {
        ...mapActions([
            'getAccountInfo'
        ]),
        async getPaymentTypes () {
            const result = await this.get('/api/ep-payment-config/list');
            if (result.success) {
                this.paymentTypes = result.value;
                this.paymentType = this.balance > this.amount ? 0 : [1, 2].filter(i => this.isEnable(i))?.[0] || null;
            }
        },
        isEnable (type) {
            return this.paymentTypes.some(t => t.type === type && t.status);
        },
        async pay () {
            this.buttonLoading = true;
            try {
                const result = await this.post(`/api/fx/order/payment/${this.order.number}?type=${this.paymentType}`);
                if (result.success) {
                    if (this.paymentType === 0) {
                        this.$Notice.success({ title: '付款成功' });
                        this.$emit('paid', this.paymentType);
                        this.val = false;
                    } else if (this.paymentType === 1) {
                        this.showInfo = false;
                        await QrCodeWithLogo.toCanvas({
                            nodeQrCodeOptions: {
                                margin: 0
                            },
                            canvas: document.getElementById('qr'),
                            content: result.value.codeUrl,
                            width: 200
                        });
                        this.showWx = true;
                    } else {
                        this.$Notice.warning({ title: '付款方式错误' });
                    }
                }
            } finally {
                this.buttonLoading = false;
            }
        },
        cancel () {
            this.val = false;
            this.showInfo = false;
            this.showWx = false;
        },
        cancelWx () {
            this.showInfo = true;
        }
    }
};
</script>

<style lang="less" scoped>
    .item {
        margin-bottom: 15px;
        &-label {
            margin-right: 10px;
            width: 80px;
        }
    }
    .payment-type {
        display: flex;
        align-items: center;
    }
    .price {
        color: coral;
        font-size: 18px;
    }
    .wx-right {
        width: 280px;
        margin-left: 30px;
        text-align: center;
        img {
            width: auto;
            height: auto;
            max-width: 100%;
            max-height: 100%;
        }
    }
</style>
