<template>
    <div>
        <div class="query-row">
            <div class="query-row-cell">
                <label class="query-row-cell-label">订单号：</label>
                <Input v-model="table.query.number" placeholder="" clearable style="width: 200px"/>
            </div>
            <div class="query-row-cell">
                <label class="query-row-cell-label">联系电话：</label>
                <Input v-model="table.query.phone" placeholder="" clearable style="width: 200px"/>
            </div>
            <div class="query-row-cell" style="margin-left: 20px" v-if="epType === 1 && userType === 1">
                <Checkbox v-model="table.query.self" @on-change="doQuery">自己购买</Checkbox>
            </div>
        </div>
        <div class="query-row">
            <div class="query-row-cell">
                <label class="query-row-cell-label">订单日期：</label>
                <DatePicker type="daterange" v-model="dateRange" :options="dateRangeOptions" split-panels placeholder="选择下单日期区间" style="width: 200px"></DatePicker>
            </div>
            <div class="query-row-cell">
                <label class="query-row-cell-label">订单状态：</label>
                <RadioGroup type="button" v-model="status">
                    <Radio :label="statusDic.waitPay">未付款</Radio>
                    <Radio :label="statusDic.waitAudit">待审核</Radio>
                    <Radio :label="statusDic.refundTicket">退票中</Radio>
                    <Radio :label="statusDic.complete">已完成</Radio>
                </RadioGroup>
                <Dropdown style="margin-left: 10px" @on-click="name => this.status = name">
                    <a href="javascript:void(0)">
                        更多
                        <Icon type="ios-arrow-down"></Icon>
                    </a>
                    <DropdownMenu slot="list">
                        <DropdownItem :name="statusDic.nonTicket" :selected="status === statusDic.nonTicket">未出票</DropdownItem>
                        <DropdownItem :name="statusDic.hasTicket" :selected="status === statusDic.hasTicket">已出票</DropdownItem>
                        <DropdownItem :name="statusDic.waitRefundTicket" :selected="status === statusDic.waitRefundTicket">退票待审核</DropdownItem>
                        <DropdownItem :name="statusDic.refundMoney" :selected="status === statusDic.refundMoney">退款中</DropdownItem>
                        <DropdownItem :name="statusDic.cancel" :selected="status === statusDic.cancel">已取消</DropdownItem>
                        <DropdownItem divided :selected="!status">全部</DropdownItem>
                    </DropdownMenu >
                </Dropdown>
            </div>
            <Button class="margin-left-10" type="primary" @click="doQuery" icon="md-search">搜索</Button>
        </div>
        <Row class="margin-top-10">
            <i-col>
                <div v-if="!haveData" class="no-data">订单列表空空如也</div>
                <template v-else>
                    <Card class="order-item" v-for="item in table.data" :key="item.id">
                        <div class="order-item-header" slot="title">
                            <div>
                                <Tag color="blue" class="margin-right-10">{{item.number}}</Tag>
                                <span>联系人：</span>
                                <span>{{item.shipping.nickname}}({{item.shipping.phone}})</span>
                                <span class="margin-right-10">【由 {{item.buyUserId ? item.buyUserName : 'API'}} 于 {{DateFormat(item.createTime)}} 下单】</span>
                            </div>
                            <div style="display: flex; align-items: center;">
                                <div class="margin-right-10" v-if="item.paymentType || item.paymentType === 0">
                                    <Icons v-if="item.paymentType === 0" :size="20" color="#ffc849" type="yuezhifu"/>
                                    <Icons v-else-if="item.paymentType === 1" :size="20" color="#12cc0d" type="wepay"/>
                                    <Icons v-else-if="item.paymentType === 2" :size="20" color="#24abed" type="alipay"/>
                                </div>
                                <Poptip trigger="hover" placement="left" v-if="item.status === 5" :title="item.buyFailRefundMoney ? null : '退款失败'" :content="item.cancelReason">
                                    <Tag>{{opt(FxOrderStatus, item.status).name}}</Tag>
                                </Poptip>
                                <Tag v-else-if="item.status === 1 && isSelfOrder(item)" color="#f29100" @click.native="goToPay(item)">去支付</Tag>
                                <Tag v-else-if="item.status === 2 && isSelfOrder(item)" @click.native="goToPaying(item)">付款中</Tag>
                                <Tag v-else>{{opt(FxOrderStatus, item.status).name}}</Tag>
                            </div>
                        </div>
                        <Table :columns="sub.col" :data="item.items"></Table>
                    </Card>
                    <div style="margin: 10px;overflow: hidden">
                        <div style="float: right;">
                            <Page :total="table.total" :show-sizer="true" placement="top"
                                  @on-page-size-change="changePageSize" @on-change="changePage"></Page>
                        </div>
                    </div>
                </template>
            </i-col>
        </Row>
        <Modal v-model="showPaying" title="提示" footer-hide>
            <Button type="success" long @click="closePaying">继续等待</Button>
            <br/>
            <br/>
            <Button type="warning" long @click="rePay">重新支付</Button>
            <br/>
            <br/>
            <Alert show-icon>
                <template slot="desc">
                    <div>如果付款过程中，你已经明确付款成功了，请点击继续等待按钮！</div>
                    <div>如果付款过程中，你已经明确付款失败了，请点击重新支付按钮！</div>
                </template>
            </Alert>
        </Modal>
        <Pay v-model="showPay" :order="order" :shipping="order.shipping" :items="order.items" @paid="doQuery"/>
        <Modal v-model="refund.show" :mask-closable="false" @on-ok="refundApply" title="退订信息" width="600" :loading="refund.loading">
            <Row :gutter="16" type="flex" class="refund-row">
                <i-col span="5" class="align-right refund-row-label">
                    订单商品:
                </i-col>
                <i-col span="19">
                    {{ refundProduct }}
                </i-col>
            </Row>
            <Row :gutter="16" type="flex" class="refund-row">
                <i-col span="5" class="align-right refund-row-label">
                    退货规则:
                </i-col>
                <i-col span="19">
                    {{ refundProduct }}
                </i-col>
            </Row>
            <Row :gutter="16" type="flex" class="refund-row">
                <i-col span="5" class="align-right refund-row-label">
                    退货详情:
                </i-col>
                <i-col span="19">
                    <Table :columns="refund.quantity.col" :data="refund.quantity.data"/>
                </i-col>
            </Row>
            <Row :gutter="16" type="flex" class="refund-row">
                <i-col span="5" class="align-right refund-row-label">
                    手续费:
                </i-col>
                <i-col span="19">
                    {{ForceMoney(refund.fee)}}
                </i-col>
            </Row>
            <Row :gutter="16" type="flex" class="refund-row">
                <i-col span="5" class="align-right refund-row-label">
                    退款金额:
                </i-col>
                <i-col span="19">
                    {{ForceMoney(refund.money)}}
                </i-col>
            </Row>
            <Row :gutter="16" type="flex" class="refund-row">
                <i-col span="5" class="align-right refund-row-label">
                    退货原因:
                </i-col>
                <i-col span="19">
                    <Input type="textarea" v-model="refund.cause" :maxlength="250" />
                </i-col>
            </Row>
            <Alert type="warning" show-icon>
                <template slot="desc">
                    <li>提交申请后,如商品需审核则订单会进入“退票待审核”状态,等待供应商确认。</li>
                    <li>余额支付,退票审核通过后会直接退还账户余额;第三方支付则在7个工作日内原路退还。</li>
                    <li>如订单长时间未审核请联系系统客服: 0731-88888888</li>
                </template>
            </Alert>
        </Modal>
        <Modal v-model="audit.show" title="审核订单" footer-hide>
            <Button type="success" long @click="doAudit(true)">接受</Button>
            <br/>
            <br/>
            <Button type="warning" long @click="doAudit(false)">拒绝</Button>
            <br/>
            <br/>
            <Alert show-icon>
                <template slot="desc">
                    <div>如果付款过程中，你已经明确付款成功了，请点击继续等待按钮！</div>
                    <div>如果付款过程中，你已经明确付款失败了，请点击重新支付按钮！</div>
                </template>
            </Alert>
        </Modal>
        <Modal v-model="refundAudit.show" title="退订审核" footer-hide>
            <Button type="success" long @click="doRefundAudit(true)">接受</Button>
            <br/>
            <br/>
            <Button type="warning" long @click="doRefundAudit(false)">拒绝</Button>
            <br/>
            <br/>
            <Alert show-icon>
                <template slot="desc">
                    <div>如果付款过程中，你已经明确付款成功了，请点击继续等待按钮！</div>
                    <div>如果付款过程中，你已经明确付款失败了，请点击重新支付按钮！</div>
                </template>
            </Alert>
        </Modal>
    </div>
</template>

<script>
import dayjs from 'dayjs';
import { DateFormat, ForceMoney } from '@/libs/common';
import { FxOrderItemStatus, FxOrderStatus, FxRefundOrderStatus, opt } from '@/libs/dic';
import TableDataView from '@/mixins/table-data-view';
import TableColRender from '@/mixins/table-col-render';
import Pay from '@/fx/views/pay';
import Icons from '@/components/icons/icons';

export default {
    name: 'OrderList',
    components: { Icons, Pay },
    mixins: [TableDataView, TableColRender],
    data () {
        return {
            opt,
            FxOrderStatus,
            DateFormat,
            ForceMoney,
            showPay: false,
            showPaying: false,
            order: {},
            sub: {
                col: [{
                    title: '产品名称',
                    tooltip: true,
                    render: (h, params) => this.renderJoin(h, params, ' - ')('productName', 'productSubName')
                }, {
                    title: '生效时间',
                    key: 'minEffectiveDate',
                    render: this.renderDate
                }, {
                    title: '过期时间',
                    key: 'maxExpiryDate',
                    render: this.renderDate
                }, {
                    title: '单价',
                    key: 'settlePrice',
                    render: (h, params) => this.renderMoney(h, params, val => val / params.row.quantity)
                }, {
                    title: '数量',
                    width: 80,
                    key: 'quantity'
                }, {
                    title: '已用',
                    width: 80,
                    key: 'usedQuantity'
                }, {
                    title: '已退',
                    width: 80,
                    key: 'refundQuantity'
                }, {
                    title: '过期',
                    width: 80,
                    key: 'invalidQuantity'
                }, {
                    title: '状态',
                    key: 'status',
                    render: (h, params) => {
                        if (params.row.refundOrder && params.row.refundOrder.status !== 7) {
                            return <span>{opt(FxRefundOrderStatus, params.row.refundOrder.status).name}</span>;
                        }
                        return this.renderDic(h, params)(FxOrderItemStatus);
                    }
                }, {
                    title: '操作',
                    key: 'action',
                    width: 220,
                    align: 'center',
                    render: (h, params) => this.renderActions(h, params, [{
                        config: {
                            loading: this.loadingBtn,
                            style: { marginRight: '5px' },
                            disabled: !this.canRefund(params.row),
                            show: this.canRefund(params.row)
                        },
                        click: () => this.refundView(params.row),
                        text: '申请退订'
                    }, {
                        config: {
                            loading: this.loadingBtn,
                            style: { marginRight: '5px' },
                            disabled: !this.canAudit(params.row),
                            show: this.canAudit(params.row)
                        },
                        click: () => this.auditView(params.row),
                        text: '审核'
                    }, {
                        config: {
                            loading: this.loadingBtn,
                            style: { marginRight: '5px' },
                            disabled: !this.canTicketing(params.row),
                            show: this.canTicketing(params.row)
                        },
                        click: () => this.ticketing(params.row),
                        text: '出票'
                    }, {
                        config: {
                            loading: this.loadingBtn,
                            style: { marginRight: '5px' },
                            disabled: !this.canRefundAudit(params.row),
                            show: this.canRefundAudit(params.row)
                        },
                        click: () => this.refundAuditView(params.row),
                        text: '退订审核'
                    }])
                }]
            },
            table: {
                query: {
                    number: null,
                    phone: null,
                    start: null,
                    end: null,
                    status: null,
                    itemStatus: null,
                    refundStatus: null,
                    self: true
                },
                url: '/api/fx/order/list'
            },
            dateRangeOptions: {
                shortcuts: [{
                    text: '今天',
                    value () {
                        const date = new Date();
                        return [date, date];
                    }
                }, {
                    text: '三天',
                    value () {
                        const date = new Date();
                        return [dayjs().subtract(3, 'day').toDate(), date];
                    }
                }, {
                    text: '七天',
                    value () {
                        const date = new Date();
                        return [dayjs().subtract(7, 'day').toDate(), date];
                    }
                }]
            },
            dateRange: [new Date(), new Date()],
            status: null,
            statusDic: {
                waitPay: '[\'1,2\']',
                waitAudit: '[\'3\', \'1\']',
                refundTicket: '[\'3\', null, \'3\']',
                complete: '[\'4\']',
                nonTicket: '[\'3\', \'3,4\']',
                hasTicket: '[\'3\', \'6\']',
                refundMoney: '[\'3\', null, \'6\']',
                waitRefundTicket: '[\'3\', null, \'1\']',
                cancel: '[\'5\']'
            },
            refund: {
                show: false,
                item: null,
                fee: 0,
                money: 0,
                loading: false,
                cause: null,
                quantity: {
                    col: [{
                        title: '游玩日期',
                        key: 'day'
                    }, {
                        title: '数量',
                        key: 'quantity'
                    }],
                    data: []
                }
            },
            audit: {
                show: false,
                loading: false,
                item: null
            },
            refundAudit: {
                show: false,
                loading: false,
                item: null
            }
        };
    },
    computed: {
        haveData () {
            return !!this.table.data?.length;
        },
        epId () {
            return this.$store.state.user.info?.ep?.id;
        },
        platformId () {
            return this.$store.state.user.info?.platform?.id;
        },
        epType () {
            return this.$store.state.user.info?.epPlatform?.type;
        },
        userType () {
            return this.$store.state.user.info?.type;
        },
        refundProduct () {
            return `${this.refund.item?.productName || ''} - ${this.refund.item?.productSubName || ''}`;
        }
    },
    watch: {
        status (val) {
            if (!val) {
                this.table.query.status = null;
                this.table.query.itemStatus = null;
                this.table.query.refundStatus = null;
            } else {
                const [status, itemStatus, refundStatus] = JSON.parse(val.replace(/'/g, '"'));
                this.table.query.status = status;
                this.table.query.itemStatus = itemStatus;
                this.table.query.refundStatus = refundStatus;
            }
            this.doQuery();
        }
    },
    methods: {
        async beforeQuery () {
            if (this.dateRange && this.dateRange.length === 2 && this.dateRange.every(d => d instanceof Date)) {
                this.table.query.start = dayjs(this.dateRange[0]).format('YYYY-MM-DD 00:00:00');
                this.table.query.end = dayjs(this.dateRange[1]).format('YYYY-MM-DD 23:59:59');
            } else {
                this.table.query.start = null;
                this.table.query.end = null;
            }
        },
        async processQueryData (data, res) {
            return data.map(o => ({
                ...o,
                items: o.items.map(item => ({
                    ...item,
                    order: o
                }))
            }));
        },
        goToPay (item) {
            this.order = item;
            this.showPay = true;
        },
        goToPaying (item) {
            this.order = item;
            this.showPaying = true;
        },
        closePaying () {
            this.showPaying = false;
            this.showPay = false;
        },
        rePay () {
            this.showPaying = false;
            this.showPay = true;
        },
        isSelfOrder (order) {
            order = order || this.order;
            return this.epId === order?.buyEpId && this.platformId === order?.buyPlatform;
        },
        canRefund (item) {
            return item.order.status === 3 &&
                [3, 4, 6].some(i => i === item.status) &&
                this.isSelfOrder(item.order) &&
                item.quantity * item.days - item.usedQuantity - item.refundQuantity > 0 &&
                (!item.refundOrder || [7, 2, 4].some(i => i === item.refundOrder.status));
        },
        canAudit (item) {
            return item.order.status === 3 &&
                item.status === 1 &&
                item.supplierEpId === this.epId &&
                item.supplierPlatform === this.platformId;
        },
        canTicketing (item) {
            return item.order.status === 3 &&
                [3, 4].some(i => i === item.status) &&
                [2, 3].some(i => i === this.epType) &&
                !item.refundOrder;
        },
        canRefundAudit (item) {
            return item.order.status === 3 &&
                item.refundOrder?.status === 1 &&
                item.supplierEpId === this.epId &&
                item.supplierPlatform === this.platformId;
        },
        async refundView (item) {
            this.refund.loading = true;
            this.$Spin.show();
            const result = await this.post('/api/fx/order/refund/apply/calc-remainder', { number: item.number });
            this.$Spin.hide();
            if (result.success) {
                this.refund.item = item;
                this.refund.show = true;
                this.refund.fee = result.value.fee;
                this.refund.money = result.value.money;
                const data = JSON.parse(result.value.data);
                this.refund.quantity.data = Object.keys(data).map(key => ({
                    day: key,
                    quantity: data[key].reduce((a, b) => a + b.quantity, 0)
                }));
            }
        },
        async refundApply () {
            const result = await this.post('/api/fx/order/refund/apply/remainder', {
                number: this.refund.item.number,
                cause: this.refund.cause
            });
            if (!result.success) {
                this.refund.loading = false;
                this.$nextTick(() => this.refund.loading = true);
                return;
            }
            this.refund.show = false;
            this.$Notice.success({ title: '退订申请成功' });
            setTimeout(() => this.doQuery(), 500);
            this.refund.loading = false;
        },
        auditView (item) {
            this.audit.loading = true;
            this.audit.item = item;
            this.audit.show = true;
        },
        async ticketing (item) {
            this.loadingBtn = true;
            const result = await this.get(`/api/fx/order/ticketing/${item.number}`);
            if (result.success) {
                this.$Notice.success({ title: '发起出票成功' });
                setTimeout(() => this.doQuery(), 500);
            }
            this.loadingBtn = false;
        },
        async doAudit (success) {
            const result = await this.post(`/api/fx/order/audit/${this.audit.item.number}?success=${success}`);
            if (!result.success) {
                this.audit.loading = false;
                this.$nextTick(() => this.audit.loading = true);
                return;
            }
            this.audit.show = false;
            this.$Notice.success({ title: '审核成功' });
            setTimeout(() => this.doQuery(), 500);
            this.audit.loading = false;
        },
        refundAuditView (item) {
            this.refundAudit.loading = true;
            this.refundAudit.item = item;
            this.refundAudit.show = true;
        },
        async doRefundAudit (success) {
            const result = await this.post(`/api/fx/order/refund/audit/${this.refundAudit.item.refundOrder.number}?success=${success}`);
            if (!result.success) {
                this.refundAudit.loading = false;
                this.$nextTick(() => this.refundAudit.loading = true);
                return;
            }
            this.refundAudit.show = false;
            this.$Notice.success({ title: '退订审核成功' });
            setTimeout(() => this.doQuery(), 500);
            this.refundAudit.loading = false;
        }
    }
};
</script>
<style scoped>
    .order-item >>> .ivu-card-body {
        padding: 0;
    }

    .order-item >>> .ivu-table td {
        border-bottom: 0;
    }

    .order-item >>> .ivu-card-head {
        padding: 10px 16px;
    }

    .order-item >>> .ivu-table-wrapper {
        border: 0;
    }

    .order-item >>> .ivu-table:before {
        background: none;
    }

    .order-item >>> .ivu-table:after {
        background: none;
    }
</style>
<style lang="less" scoped>
    .query-row {
        display: flex;
        margin-bottom: 10px;
        &-cell {
            display: flex;
            align-items: center;
            &-label {
                width: 80px;
                text-align: right;
            }
        }
    }
    .no-data {
        display: flex;
        justify-content: center;
    }
    .order-item {
        margin-bottom: 20px;
        &-header {
            display: flex;
            align-items: center;
            justify-content: space-between;
        }
    }
    .refund-row {
        margin-bottom: 10px;
        align-items: center;
        &:last-child {
            margin-bottom: 0;
        }
        &-label {
            font-size: 14px;
            font-weight: 400;
        }
    }
</style>
